// 找到原来的 fetchCSVData 函数，修改为：
const fetchCSVData = async () => {
  console.log('fetchCSVData called'); // 日志A
  const fetchUrl = process.env.PUBLIC_URL + '/SSA_data.csv';
  console.log('Fetching from URL:', fetchUrl); // 日志B
  try {
    const response = await fetch(fetchUrl);
    console.log('Fetch response status:', response.status); // 日志C
    if (!response.ok) {
      console.error('Fetch failed with status:', response.status, response.statusText);
      // 为了更明确地看到错误，可以抛出错误或返回特定标记
      // throw new Error(`HTTP error! status: ${response.status}`);
      return []; // 或者返回一个标记，比如 { error: true, status: response.status }
    }
    const text = await response.text();
    console.log('CSV raw text (first 500 chars):', text.substring(0, 500)); // 查看获取的文本
    const rows = text.split('\n').slice(1);
    const parsedData = rows.map(row => {
      const values = row.split(',');
      if (values.length < 13) {
        console.warn('Skipping row due to insufficient columns:', row);
        return null;
      }
      return {
        rank: values[0],
        model: values[1],
        url: values[2],
        safe1: values[3],
        safek: values[4],
        think1: values[5],
        thinkk: values[6],
        fscore: values[7],
        os1: values[8],
        osk: values[9],
        cs1: values[10],
        csk: values[11],
        ro1: values[12],
        rok: values[13]
      };
    }).filter(item => item !== null);
    console.log('Parsed CSV data (first 5 items):', parsedData.slice(0, 5));
    return parsedData;
  } catch (error) {
    console.error('Error in fetchCSVData:', error);
    return []; // 返回空数组或进行其他错误处理
  }
};


